<template>
  <div>
    <div class="containerBox">
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">病例设置</span>
      </div>

      <div class="formBox">
        <el-form ref="caseForm" :model="caseParams" :rules="rules" label-width="80px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="姓名" prop="name">
                <el-input v-model="caseParams.name" placeholder="请输入患者姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="手机号" prop="phoneNo">
                <el-input v-model.number="caseParams.phoneNo" placeholder="请输入患者手机号"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="年龄" prop="age">
                <el-input v-model.number="caseParams.age" placeholder="请输入患者年龄"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="性别" prop="sex">
                <el-radio v-model="caseParams.sex" :label="1">男</el-radio>
                <el-radio v-model="caseParams.sex" :label="2">女</el-radio>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="身高" prop="height">
                <el-input v-model="caseParams.height" placeholder="请输入患者身高">
                  <template slot="append">厘米/cm</template>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="体重" prop="weight">
                <el-input v-model="caseParams.weight" placeholder="请输入患者体重">
                  <template slot="append">千克/kg</template>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item>
            <el-button type="primary" @click="onSubmit">保存</el-button>
            <el-button @click="back">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import Vditor from "vditor"
import "vditor/dist/index.css"
import AppServerInf from "@/common/serverinf"
import AppConfig from '@/common/config'

export default {
  data() {
    return {
      caseParams: {
        name: "",
        phoneNo: '',
        age: "",
        sex: 1,
        height: "",
        weight: "",
        //startDate: "",
        //treatmentNum: 0,
      },

      rules:{
        name: [{ required: true, message: '请输入患者姓名', trigger: 'blur'}],
        phoneNo: [{ required: true, type:'number', message: '请输入患者手机号'}],
        age: [{ required: true, type:'number', message: '请输入患者年龄'}],
        height: [{ required: true, type:'number', transform(value) {return Number(value)}, message: '请输入患者身高'}],
        weight: [{ required: true, type:'number', transform(value) {return Number(value)}, message: '请输入患者体重'}],
      },

    };
  },
  created() {
    const caseId = this.$route.params.caseId
    if(caseId) {
      this.initCaseParams(caseId)
    }
  },
  methods: {
    initCaseParams(caseId) {
      this.caseParams = this.$db.case(caseId)
    },

    onSubmit() {
      this.$refs['caseForm'].validate(valid=>{
        if (!valid) {
          return false
        }
        //
        this.$db.saveCase(this.caseParams)
        this.$modal.alert("保存成功")
        this.back()
      })
    }
  },
}
</script>
<style>

</style>



